import { Directive, TemplateRef, ViewContainerRef, ElementRef, Input, Renderer2, OnInit } from '@angular/core';
import { ITextData } from './repository-info';

@Directive({
  selector: '[appTextInfo]'
})
export class TextInfoDirective implements OnInit {
  constructor(private _renderer: Renderer2, private _elementRef: ElementRef<HTMLElement>) { }
  ngOnInit() {
    this._renderer.addClass(this._elementRef.nativeElement, 'wild');
  }
  @Input() set appTextInfo(value: string|string[]|ITextData|null|undefined) {
    if (typeof(value) === 'string') {
      if (value.trim().length > 0)
        this._renderer.appendChild(this._elementRef.nativeElement, this._renderer.createText(value));
      return;
    }
    if (typeof(value) === 'undefined' || value === null)
      return;
    if (!Array.isArray(value)) {
      if (value.isHtml) {
        if (this._elementRef.nativeElement !== null) {
          if (typeof(value.text) === 'string')
            this._elementRef.nativeElement.innerHTML = value.text;
          else if (value.text.length > 0)
            this._elementRef.nativeElement.innerHTML = (value.text.length === 1) ? value.text[0] : value.text.join('\n');
        }
        return;
      }
      value = value.text;
      if (typeof(value) === 'string') {
        if (value.trim().length > 0)
          this._renderer.appendChild(this._elementRef.nativeElement, this._renderer.createText(value));
        return;
      }
    }

    value = value.map(function(s: string): string { return s.trim(); }).filter(function(s: string): boolean { return s.length > 0; });
    if (value.length === 0)
      return;
    if (value.length === 1)
      this._renderer.appendChild(this._elementRef.nativeElement, this._renderer.createText(value[0]));
    else
      value.forEach(function(this: TextInfoDirective, s: string, index: number) {
        if (index > 0)
          this._renderer.appendChild(this._elementRef.nativeElement, this._renderer.createText('\n'));
        const el = this._renderer.createElement('p');
        this._renderer.appendChild(this._elementRef.nativeElement, el);
        this._renderer.appendChild(el, this._renderer.createText(s));
      }, this);
  }
}
